@import '../variables.scss';

#about-container {
  background-image: url('../images/headshot-blacknwhite.png');
  background-color: rgba($color: $about-background, $alpha: 1);
  background-position: 100% 100%;
  background-size: 700px;
  background-repeat: no-repeat;
  height: 550px;
  padding: 5%;
  color: $about-font;
  .name {
    margin-top: 90px;
    padding-bottom: 0;
    font-size: 3.5em;
    width: 40%;
    line-height: 50px;
  }
  .about-me {
    max-width: 45%;
  }
  h1,
  h2 {
    margin: 0;
  }
}

@media only screen and (max-width: 600px) {
  #about-container {
    background-image: none;
    height: 320px;
    .name {
      margin-top: 35px;
    }
    .about-me {
      max-width: 100%;
    }
  }
}